<template>
  <header id="header">
     <h1 id="top_logo">
       <router-link to="/"><img src="../assets/img/ico_logo.png" alt="LG전자 로고"><span>LG전자</span></router-link>
     </h1>
     <nav id="top_nav">
       <ul>
         <li v-for="(list, index) in menuList">
           <router-link :to="list.alink">{{list.text}}</router-link>
           <ul v-if="index == 0" class="submenu">
             <li v-for="list in companySubmenuList">
               <router-link :to="list.alink">{{list.text}}</router-link>
             </li>
           </ul>
           <ul v-if="index == 3" class="submenu">
             <li v-for="list in commnuitySubmenuList">
               <router-link :to="list.alink">{{list.text}}</router-link>
             </li>
           </ul>
         </li>
       </ul>
     </nav>
  </header>
</template>
<script>
export default {
  name: 'HeaderComponent',
  data () {
    return {
      menuList: [
        { text: '기업정보', alink: '/introduce'},
        { text: '제품', alink: '/product'},
        { text: '갤러리', alink: '/gallery'},
        { text: '커뮤니티', alink: '/notice'},
        { text: '찾아오는 길', alink: '/location'}
      ],
      companySubmenuList: [
        { text: '회사소개', alink: '/introduce'},
        { text: '브랜드', alink: '/bi'},
        { text: '역사', alink: '/history'}
      ],
      commnuitySubmenuList: [
        { text: '공지사항', alink: '/notice'},
        { text: 'FAQ', alink: '/faq'}
      ]
    }
  }
}
</script>



